import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import IconButton from '@material-ui/core/IconButton';
import Visibility from '@material-ui/icons/Visibility';

const styles = {
    chip: {
        height: "18px",
        lineHeight: "18px",
        cursor: "pointer",
        marginRight: "6px",
    },
    button: {
        border: "solid 1px #dedede"
    }
},
    urls = {
        know: "https://github.com/yuminjustin/spotlight-cli",
        muti: "https://github.com/yuminjustin/webpack-study/tree/master/mutiModule",
        bb: "http://www.css88.com/doc/backbone/",
        san: "https://baidu.github.io/san/",
        ml: "https://mithril.js.org",
        mobx: "https://cn.mobx.js.org/"
    }

class About extends Component {
    render() {
        const { classes } = this.props;
        return (
            <div className="_about">
                <div className="purple_blank">&nbsp;</div>
                <div className="_about_box pr">
                    <img src="/static/image/white2.png" className="db pa logo" />
                    <div className="pa lintosp">
                        <IconButton className={classes.button} aria-label="去了解" component="a" target="_blank" href={urls.know}>
                            <Visibility />
                        </IconButton>
                    </div>
                    <div className="pr c _a_b_inner m">
                        <p className="_c_p">
                            一个简单的前端项目脚手架，整体构建参照vue-cli 2.0。 <br />
                            A simple front-end project scaffolding, the overall build reference vue-cli 2.0.
                        </p>
                        <div className="_sp_des">
                            &nbsp;&nbsp;&nbsp;&nbsp;webpack+gulp的方式满足大部分项目需求，0封装，偏后台的模板内含简单的数据交互逻辑示例，对于需要
                            <Chip className={classes.chip} label="多入口多模板" component="a" target="_blank" href={urls.muti} />
                            的需求需要改写配置文件，目前提供9套模板。<br />
                            <ul>
                                <li>
                                    <strong>1. normal</strong>
                                </li>
                                <li>
                                    <strong>2. react</strong>
                                </li>
                                <li>
                                    <strong>3. redux</strong>
                                    <span>react+redux+router+antd，包含简单redux流程</span>
                                </li>
                                <li>
                                    <strong>4. vue</strong>
                                </li>
                                <li>
                                    <strong>5. vuex</strong>
                                    <span>vue+vuex+router+element-ui，包含简单vuex流程</span>
                                </li>
                                <li>
                                    <strong>6. backbone</strong>
                                    <span>backbone+router+jQurey+underscore</span>
                                </li>
                                <li>
                                    <strong>7. mithril</strong>
                                    <span>mithril+router，已制作简单mithril流程</span>
                                </li>
                                <li>
                                    <strong>8. san</strong>
                                </li>
                                <li>
                                    <strong>9. mobx</strong>
                                    <span>react+mobx+router+antd，包含简单mobx流程和todo示例</span>
                                </li>
                            </ul>
                        </div>
                        <div className="_sp_des _mini">
                            <h4>低版本IE</h4><br />
                            <p>
                                如果你的项目需要兼容到IE8甚至更低，请使用<strong>san</strong>和<strong>backbone</strong>模板。<br />
                                在使用这两套模板时请使用es5做开发，为了更好兼容避免踩坑，未做babel支持。两套模板均不含UI，需要自行适配添加。<br />
                                <Chip className={classes.chip} label="backbone" component="a" target="_blank" href={urls.bb} />
                                是老一套需要操作DOM的框架，
                                <Chip className={classes.chip} label="san" component="a" target="_blank" href={urls.san} />
                                是百度出品类似vue但是能兼容低版本IE的Virtual DOM框架;<br />
                                backbone使用cnpm安装package时，打包后的代码将不支持低版本IE。
                            </p>
                        </div>
                        <div className="_sp_des _mini">
                            <h4>gulp</h4><br />
                            <p>
                                多套模板都涉及到和gulp的配合使用，涉及到某些字符串的替换、路径修改、文件追加等，所以请确保你的gulp是全局安装的！
                            </p>
                        </div>
                        <div className="_sp_des _mini">
                            <h4>按需加载</h4><br />
                            <p>
                                涉及到使用路由的模板都是分开打包按需加载的，<strong>backbone</strong>、<strong>vuex</strong>、
                                <strong>redux</strong>、
                                <Chip className={classes.chip} label="mithril" component="a" target="_blank" href={urls.ml} />、
                                <Chip className={classes.chip} label="mobx" component="a" target="_blank" href={urls.mobx} />。
                            </p>
                        </div>
                        <div className="_sp_des _mini">
                            <h4>404页面</h4><br />
                            <p>
                                涉及到使用路由的模板全部含有404页面，当地址栏的route是未注册的地址会自动打开404页面。<strong>同上</strong>
                            </p>
                        </div>
                        <div className="_sp_des _mini">
                            <h4>History router</h4><br />
                            <p>
                                当你使用History方式的router，需要把build文件下的config.js的dev和build中html5Router设置为true，
                                dev中html5Router是为了告诉webpackServer此项目采用History方式的router，
                                build中html5Router是为了告诉gulp需要给项目加上Apache的配置文件和打包后路径的修改。
                            </p>
                            <p>
                                模板：<strong>vuex</strong>、<strong>redux</strong>、<strong>mobx</strong>。
                            </p>
                            <p>
                                所以build后的代码将默认支持Apache服务器，nginx 需要另外配置
                            </p>
                        </div>
                        <div className="_sp_des _mini">
                            <h4>所有模板已升级至webpack4</h4>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default withStyles(styles)(About);
